import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { 
  Box, 
  Typography, 
  Grid, 
  Paper, 
  Card, 
  CardContent, 
  CircularProgress 
} from '@mui/material';
import SchoolIcon from '@mui/icons-material/School';
import BarChartIcon from '@mui/icons-material/BarChart';
import ChecklistIcon from '@mui/icons-material/Checklist';
import { fetchProfile } from '../store/authSlice';

function DashboardPage() {
  const dispatch = useDispatch();
  const { user, isLoading } = useSelector(state => state.auth);

  useEffect(() => {
    if (!user || !user.id) {
      dispatch(fetchProfile());
    }
  }, [dispatch, user]);

  // Default values if user data is not loaded
  const userData = user || {
    username: '...',
    nickname: '...',
    studyDays: 0,
    totalQuestions: 0,
    answeredQuestions: 0,
    accuracyRate: 0.0
  };

  return (
    <Box sx={{ flexGrow: 1, p: 3 }}>
      <Typography variant="h4" gutterBottom>
        {userData.nickname || userData.username}
      </Typography>
      
      {isLoading && !user ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          <Grid container spacing={3} sx={{ mt: 2 }}>
            {/*  */}
            <Grid item xs={12} sm={6} md={3}>
              <Card>
                <CardContent>
                  <SchoolIcon sx={{ fontSize: 40, color: 'primary.main', mb: 1 }} />
                  <Typography variant="h6" color="textSecondary"></Typography>
                  <Typography variant="h4" color="primary">{userData.studyDays || 0} </Typography>
                </CardContent>
              </Card>
            </Grid>
            
            {/*  */}
            <Grid item xs={12} sm={6} md={3}>
              <Card>
                <CardContent>
                  <BarChartIcon sx={{ fontSize: 40, color: 'secondary.main', mb: 1 }} />
                  <Typography variant="h6" color="textSecondary"></Typography>
                  <Typography variant="h4" color="secondary">{userData.totalQuestions || 0}</Typography>
                </CardContent>
              </Card>
            </Grid>
            
            {/*  */}
            <Grid item xs={12} sm={6} md={3}>
              <Card>
                <CardContent>
                  <ChecklistIcon sx={{ fontSize: 40, color: 'success.main', mb: 1 }} />
                  <Typography variant="h6" color="textSecondary"></Typography>
                  <Typography variant="h4" color="success.main">{userData.answeredQuestions || 0}</Typography>
                </CardContent>
              </Card>
            </Grid>
            
            {/*  */}
            <Grid item xs={12} sm={6} md={3}>
              <Card>
                <CardContent>
                  <Typography variant="h6" color="textSecondary"></Typography>
                  <Typography variant="h4" color="info.main">{(userData.accuracyRate || 0).toFixed(1)}%</Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
          
          <Grid container spacing={3} sx={{ mt: 3 }}>
            <Grid item xs={12} md={8}>
              <Paper sx={{ p: 2, height: '100%' }}>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Typography variant="body1">
                   {userData.studyDays || 0}  {userData.answeredQuestions || 0} 
                   {(userData.accuracyRate || 0).toFixed(1)}%
                  
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              <Paper sx={{ p: 2, height: '100%' }}>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Typography variant="body2" color="textSecondary">
                   <br />
                   <br />
                   <br />
                   
                </Typography>
              </Paper>
            </Grid>
          </Grid>
        </>
      )}
    </Box>
  );
}

export default DashboardPage;